
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "StarRating",
  props: {
    rating: {
      type: Number as PropType<number>,
      default: 0,
    },
    limit: {
      type: Number as PropType<number>,
      default: 5,
    },
  },
  computed: {
    ratingCount() {
      return {
        width: `${this.rating * 20}%`,
      };
    },
  },
  setup() {
    return {};
  },
});
